var graceChecker = require("@/utils/graceChecker.js");
import interfaces from '@/utils/interfaces.js'
export default {
	data() {

		return {
			current: 0, //选择国家
			num: true,
			get_code: true,
			isgetcode: false, // 是否获取过code
			count: 60,
			country: "China",
			isChecked: false,
			registerForm: {
				mobile: "",
				code: "",
				password: "",
				re_password: "",
				state_code: "",
				checkbox: "",
				user_name: "",
				company_name: "",
				contact_name: "",
				organization_name: "",
				type: "", // 1个人 2机构 3公司
			},
			countryList: [{
					value: 'China',
					name: '中国',
				},
				{
					value: 'America',
					name: '国外',
				},
			],
			//定义表单规则
			rules: [{
					name: "organization",
					checkType: 'notnull',
					checkRule: "",
					errorMsg: "请输入机构名称"
				},
				{
					name: "company",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入公司名称"
				},
				{
					name: "contact",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入联系人"
				},
				{
					name: "username",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入用户名"
				},
				{
					name: "mobile",
					checkType: "phoneno",
					checkRule: "",
					errorMsg: "请输入正确的手机号"
				},
				{
					name: "code",
					checkType: "string",
					checkRule: "6,6",
					errorMsg: "请输入验证码"
				},
				{
					name: "password",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入密码"
				},
				{
					name:'re_password',
					checkType:'checkpass',
					checkRule:'',
					errorMsg:'两次密码输入不一致'
				},
				{
					name:'isChecked',
					checkType:'ischecked',
					checkRule:'',
					errorMsg:'请勾选协议'
				}
			]
		}
	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		this.registerForm.type = option.type; //打印出上个页面传递的参数。
		// console.log(this.registerForm.type);
	},
	
	methods: {
		radioChange: function(evt) { //选择国家
			for (let i = 0; i < this.countryList.length; i++) {
				if (this.countryList[i].value === evt.target.value) {
					this.current = i;
					//this.registerForm.country = evt.target.value; //获取国家
					break;
				}

			}
			this.country = evt.target.value; //获取国家
			this.registerForm.password = '',
				this.registerForm.re_password = '',
				this.registerForm.company_name = '',
				this.registerForm.contact_name = '',
				this.registerForm.organization_name = ''
		},
		//发送验证码
		sendCode() {
			if (this.registerForm.mobile !== "") {
				this.get_code = false;
				this.isgetcode = true;
				let interval = setInterval(() => {
					this.count--;
					if (this.count < 1) {
						this.get_code = true;
						this.count = 60;
						clearInterval(interval);
					}
				}, 1000);
			} else {
				uni.showToast({
					title: "请输入手机号",
					icon: "none"
				});
			}
			this.request({
				url: interfaces.getCodeData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						mobile: this.registerForm.mobile,
						type: 2 //1.login  2.register
					}
				},
				success: ((res) => {
					//console.log(res);
				})
			})
		},
		formSubmit: function(e) {
			//进行表单检查
			var formData = e.detail.value;
			formData.isChecked=this.isChecked;
			var checkRes = graceChecker.check(formData, this.rules);
			if (!checkRes) {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}else{
			this.request({
				url: interfaces.getRegisterData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						mobile: this.registerForm.mobile,
						code: this.registerForm.code,
						password: this.registerForm.password,
						re_password: this.registerForm.re_password,
						state_code: this.country == 'China' ? 86 : '', //国家代号,暂只处理中国设为86,国外为空
						user_name: this.registerForm.user_name,
						company_name: this.registerForm.company_name,
						contact_name: this.registerForm.contact_name,
						organization_name: this.registerForm.organization_name,
						country: this.country,
						type: this.registerForm.type //注册类型 1个人 2 机构 3企业
					}
				},
				success: ((res) => {
					console.log(res);
					if (res.code == 200) {
						console.log(123);
						uni.showToast({
							title: '注册成功',
							icon: "none"
						});
						uni.navigateTo({
							url: "/pages/user/login"
						})
					} else {					
						uni.showToast({
							title: res.message,
							icon: "none"
						});
					}
				}),
				fail: ((error) => {

				})
			});
			}

		},

		formReset: function(e) {
			this.chosen = ''
		}

	}
}
